// src/components/Form.tsx
import React, { useState } from 'react';
import ApiService from '../services/api.service.tsx';
import { API_ENDPOINTS } from '../constants/db.constants.tsx';
import {InputField, BooleanInputField} from './InputField.tsx';
import CircularIndeterminate from './Loading.tsx';
import { FullPageContainer, FormContainer } from './StyledComponents.tsx'; // Import the styled component


const Form: React.FC = () => {
  const [formRespond, setFormRespond] = useState('');
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [idNumber, setIdNumber] = useState('');
  const [address, setAddress] = useState('');
  const [email, setEmail] = useState('');
  const [phoneSms, setPhoneSms] = useState('');
  const [phoneWhatsApp, setPhoneWhatsApp] = useState('');
  const [haveOpinion, setHaveOpinion] = useState(false);
  const [goodOpinion, setGoodOpinion] = useState('');
  const [badOpinion, setBadOpinion] = useState('');
  const [getMessages, setGetMessages] = useState(false);
  const [errors, setErrors] = useState<{ [key: string]: string }>({});
  const [isLoading, setIsLoading] = useState(false);

  const emptyForm = () =>{
    setFirstName('')
    setLastName('')
    setIdNumber('')
    setAddress('')
    setEmail('')
    setPhoneSms('')
    setPhoneWhatsApp('')
    setHaveOpinion(false)
    setGoodOpinion('')
    setBadOpinion('')
    setGetMessages(false)
    setErrors({})
  }

  const validateForm = () => {
    const newErrors: { [key: string]: string } = {};
    if (!getMessages) newErrors.getMessages = '     ';
    if (!firstName) newErrors.firstName = '    ';
    if (!lastName) newErrors.lastName = '    ';
    if (!email) newErrors.email = '    ';
    if (!idNumber) newErrors.idNumber = '     ';
    if (!phoneSms && !phoneWhatsApp) {newErrors.phoneSms = '    '; newErrors.phoneWhatsApp = '    '}
    
    if (idNumber.length !== 9 && idNumber) newErrors.idNumber = '      ';
    if (!validateEmail(email) && email) newErrors.email = '   ';
    if (!validatePhone(phoneSms) && phoneSms) newErrors.phoneSms = '   ';
    if (!validatePhone(phoneWhatsApp) && phoneWhatsApp) newErrors.phoneWhatsApp = '   ';
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async () => {
    setIsLoading(true)
    if (validateForm()) {
      try {
        const response = await ApiService.getInstance().post(API_ENDPOINTS.USERS.CREATE_USER, {
            firstName,
            lastName,
            idNumber,
            address,
            email,
            phoneSms,
            phoneWhatsApp,
            goodOpinion,
            badOpinion,
            getMessages
        })
        setFormRespond(' !!!    ')
        emptyForm()
    } catch (error) {
        ApiService.getInstance().post(API_ENDPOINTS.LOGS.LOG_ERROR, {
            error,
            firstName,
            lastName,
            idNumber,
            email,
            phoneSms,
            phoneWhatsApp
        })
        if (error.data?.errorCode === 4011) setFormRespond('   ')
        console.error(error);
    } finally {
        setIsLoading(false)
    }
    
    } else {
      console.log('validate form failed')
    setIsLoading(false)
    }
  };

  return (
    <FullPageContainer>
      <FormContainer>
      {isLoading ? 
      <CircularIndeterminate></CircularIndeterminate>
      : <>
        <h2> ,       </h2>
        <h2>    </h2>
        <form>
          <InputField label=" " type="text" value={firstName} onChange={setFirstName} error={errors.firstName} mandatory={true} />
          <InputField label=" " type="text" value={lastName} onChange={setLastName} error={errors.lastName} mandatory={true}/>
          <InputField label="  " type="text" value={idNumber} onChange={setIdNumber} error={errors.idNumber} mandatory={true}/>
          <InputField label=" " type="text" value={address} onChange={setAddress} />
          <InputField label=" " type="email" value={email} onChange={setEmail} error={errors.email} mandatory={true}/>
          <InputField label="    sms" type="text" value={phoneSms} onChange={setPhoneSms} error={errors.phoneSms} />
          <InputField label="    whatsapp" type="text" value={phoneWhatsApp} onChange={setPhoneWhatsApp} error={errors.phoneWhatsApp} />
          <BooleanInputField label="   " type="checkbox" value={haveOpinion} onChange={setHaveOpinion} />
          {haveOpinion ? <> 
            <InputField label="   ,      ? " type="textarea" value={goodOpinion} onChange={setGoodOpinion} />
            <InputField label="   ,   ?" type="textarea" value={badOpinion} onChange={setBadOpinion}/> </> 
            : <></>}          
          <BooleanInputField label="/      " type="checkbox" value={getMessages} onChange={setGetMessages} error={errors.getMessages} mandatory={true}/>
          <h3 >     ,   </h3>
          <h3 >   {String.fromCodePoint(0x1F609)}</h3>
          <button type="button" onClick={handleSubmit} disabled={isLoading}></button>
          {formRespond ? <h3>{formRespond}</h3> : <></>}
          </form>
          </>
    }
      </FormContainer>
    </FullPageContainer>
  );
};

const validateEmail = (email:string) => {
  return String(email)
    .toLowerCase()
    .match(
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    );
};

const validatePhone = (phone:string) => {
  return phone.startsWith('0',0) && phone.length <= 10 && phone.length >= 9
};

export default Form;
